.extra-point-div {
    width: 100%;
    height: 100%;
    display: inline-flex;
    justify-content: space-evenly;
    text-align: center;
}

.extra-point-div h2 {
    font-size: 2.5vh;
    color: white;
    font-weight: 100;
    border-bottom: .1vh solid grey;
}

.pa-attempt-div {
    width: 33%;
    height: 100%;
    display: inline-block;
    border: .25vh solid grey;
    border-radius: 1vh;
    background-color: #202128;
}

.go-for-two-div {
    width: 33%;
    height: 100%;
    border-radius: 1vh;
    border: .25vh solid grey;
    background-color: #202128;    display: inline-block;

}

.touchdown-btn:hover {
    transform: scale(1.1);
    transition: .25s ease-in-out;
}

.formation-div {
    width: 20vw;
    display: inline-block;
}