@import url('https://fonts.googleapis.com/css?family=Audiowide');


* {
    margin: 0;
    padding: 0;
}

body {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: "Audiowide";
}

.pb-container-div {
    width: 100%;
    height: 100%;
}   

.field-div {
    width: 100%;
    height: 100%;
    background-color: red;
}

.map-div {
    background-color: white;
    position: relative;
    z-index: 0;
    width: 100%;
    height: 100%;
}