.form-item {

    width: 20%;
    height: auto;
    display: inline-block;
    font-size: 2.5vh;
    margin: 1vh;
    border: .1vh solid #54565b;
    border-radius: 1vh;
}

.endzone-text-label {
    color: white;
    height: auto;
    display: inline-block;
    vertical-align: middle;
    margin: 1vh;
    margin-right: 2vw;
    border-bottom: .1vh solid #0984e3;
    font-size: 2.5vh;
}

.endzone-text-input {
    padding: .75vh;
    font-family: "Audiowide";
    color: white;
    background-color: rgba(0, 0, 0, 0);
    border: .1vh solid #0984e3;
    height: auto;
    width: 25%;
    display: inline-block;
    vertical-align: middle;
    margin: 0;
    border-radius: 1vh;
}

.endzone-select {
    background-color: rgba(0, 0, 0, 0);
    font-family: "Audiowide";
    border: .1vh solid #0984e3;
    background-color: #0a0d14;
    border-radius: 1vh;
    margin: 0;
    padding: .5vh;
    color: white;
    width: 50%;
    font-size: 1.5vh;
}