@import url('https://fonts.googleapis.com/css?family=Audiowide');

* {
    margin: 0;
    padding: 0;
}

.help-container {
    position: fixed;
    top: 10%;
    width: auto;
    height: 33vh;
    right: 0vh;
}

.help-button-div {
    display: inline-flex;
    justify-content: center;
    vertical-align: middle;
    width: auto;
    height: auto;
    background-color: #20232b;
    border-right: .25vh solid #353b48;
    margin-top: 20vh;
    border-top-left-radius: 100%;
    border-bottom-left-radius: 100%;
}

.help-button-div:hover {
    cursor: pointer;
    transform: scale(1.1);
    animation: tool-hover 2s forwards;
}

.help-container-tool-icon {
    display: inline-flex;
    justify-content: center;
    opacity: 1;
    width: 5vh;
    height: 5vh;
    color: white;
    border: .5vh dotted #0984e3;
    padding: 1vh;
    border-top-left-radius: 100%;
    border-bottom-left-radius: 100%;
}

.help-container-tool-expand {
    margin: 1vh;
}

.help-div {
    display: none;
    text-align: center;
    width: 18vw;
    min-width: 200px;
    height: 56vh;
    background-color: #20232b;

    border-top: .25vh solid #5d626d;
    border-left: .5vh solid #5d626d;
    border-bottom: .05vh solid #5d626d;
    border-top-left-radius: 5vh;
    border-bottom-right-radius: .25vh;
}

.help-div h3 {
    font-size: 2.5vh;
    font-weight: 100;
    margin: 1vh;
    color: white;
    padding-bottom: 1vh;
    border-bottom: .1vh solid #0984e3;
}


.help-div-exit {
    position: fixed;
    display: inline-flex;
    justify-content: left;
    vertical-align: middle;
    top: 59vh;
    right: 0;
    width: 17.5vw;
    min-width: 200px;
    height: auto;
}

.help-div-exit-icon {
    margin-right: 1vh;
    margin-bottom: 1vh;
    border: .5vh dotted grey;
    border-radius: 100%;
}

.help-div-exit-icon:hover {
    transform: scale(1.1);
}

.help-instructions {
    padding-left: 2vw;
    padding-right: 2vw;
    text-align: left;
    width: 80%;
    height: 75%;
    overflow-y: auto;
    color: white;
    font-size: 1.75vh;
}

.help-instructions li {
    margin: 1vh;
}